@import "https://fonts.googleapis.com/css?family=Roboto:100,300,400,700,500,500italic,400italic,300italic,100italic,700italic";
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vmax;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-size: 6rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  display: flex;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

.modal-overlay {
  backdrop-filter: blur(4px);
  z-index: 1000;
  background-color: #0009;
  justify-content: center;
  align-items: center;
  animation: fadeIn .3s ease-in-out;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-container {
  max-width: 640px;
  width: 90%;
  max-height: 80vh;
  background-color: #fff;
  border-radius: 12px;
  padding: 2rem;
  overflow-y: auto;
  box-shadow: 0 8px 24px #00000026;
}

.modal-title {
  color: #333;
  margin-top: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.modal-summary {
  color: #555;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.modal-contents {
  color: #444;
  background-color: #f7f7f9;
  border-radius: 8px;
  margin-bottom: 1rem;
  padding: 1rem;
  font-size: .9rem;
  overflow-x: auto;
}

.modal-link {
  color: #2563eb;
  border-bottom: 1px solid #2563eb;
  margin-top: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .2s, border-color .2s;
  display: inline-block;
}

.modal-link:hover {
  color: #1d4ed8;
  border-color: #1d4ed8;
}

.modal-close-btn {
  color: #fff;
  cursor: pointer;
  float: right;
  background-color: #2563eb;
  border: none;
  border-radius: 6px;
  margin-top: 1.5rem;
  padding: .6rem 1.2rem;
  font-size: .95rem;
  font-weight: 500;
  transition: background-color .2s;
}

.modal-close-btn:hover {
  background-color: #1d4ed8;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

